<template>
	<div id="setting">
    <div class="header">
      <div class="van-hairline--bottom van-nav-bar">
        <div class="van-nav-bar__left">
          <i class="van-icon van-icon-arrow van-nav-bar__arrow" style="color: #000 !important;" @click="$router.back(-1)"></i>
        </div>
        <div class="van-ellipsis van-nav-bar__title">设置</div>
      </div>
    </div>
    <div class="body_box">
      <van-cell-group class="user-group" style="margin: 5px 0;">
        <van-cell is-link @click="loginOut">
          <img src="static/img/icon_qchc_sz1.png" class="barImg"/>
          <span class="padding-left-5">清除缓存</span>
        </van-cell>
        <!--<van-cell is-link>-->
          <!--<img src="static/img/icon_jcgx_sz.png" class="barImg"/>-->
          <!--<span class="padding-left-5">检查更新</span>-->
        <!--</van-cell>-->
        <van-cell is-link @click="toUserFeedback">
          <img src="static/img/icon_wyfk_sz1.png" class="barImg"/>
          <span class="padding-left-5">我要反馈</span>
        </van-cell>
        <van-cell is-link @click="loginOut">
          <img src="static/img/icon_zxzh_sz1.png" class="barImg"/>
          <span class="padding-left-5">注销账号</span>
        </van-cell>
        <!--<van-cell is-link>-->
          <!--<img src="static/img/icon_gy_sz.png" class="barImg" style="height: 20px"/>-->
          <!--<span class="padding-left-5">关于</span>-->
        <!--</van-cell>-->
      </van-cell-group>
    </div>
	</div>
</template>

<script>
	export default {
		name: 'setting',
		data() {
			return {
			}
		},
    mounted() {
    },
		methods: {
      loginOut(){
        localStorage.clear();
        this.$router.push('login');
      },
      toUserFeedback(){
        this.$router.push('userFeedback');
      },
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .body_box{
    margin-top: 50px;
  }
  .barImg {
    width: 20px;
    vertical-align: middle;
  }
  .release {
    width: 18.7%;
    display: inline-block;
    text-align: center;
  }
  .sign {
    position: absolute;
    right: 8px;
    margin-top: 5px;
    color: #ffffff;
  }
  .user_box {
    position: absolute;
    top: 40px;
  }
  .head_box {
    position: absolute;
    top: 10px;
  }
  .share {
    position: absolute;
    right: 15px;
    width: 20px;
  }
</style>



